const rp = require('request-promise');
const cheerio = require('cheerio');

export class Fetcher {

    constructor(requestpromise = rp) {
        this.rp = requestpromise;
    }

    fetch(url) {
        return this.rp({
            uri: url,
            simple: true
        })
            .then(body => {
                return cheerio.load(body)
            });
    }
}